<template>
  <div class="wrapper">
    <Navbar 
      @setSearch="searchValue = $event"
    />
    <Notes 
      :notes="filterNotes" 
      @delNote="delNote"
      @change="change"
    />
    <Transition name="modal">
      <Modal 
      @close="isModalOpen = false" 
      v-if="isModalOpen" 
      @addNote="addNote"
      :edit="edit"
      :editedNote="editedNote"
      @changeNote="changeNote"
      />
    </Transition>
    <button class="add__note" @click="isModalOpen = true, edit = false">
      <img src="@/assets/images/edit.svg" alt="" />
    </button>
  </div>
</template>

<script>
import Modal from "./components/Modal.vue";
import Navbar from "./components/Navbar.vue";
import Notes from "./components/Notes.vue";
export default {
  components: {
    Navbar,
    Notes,
    Modal,
  },
  data() {
    return {
      isModalOpen: false,
      notes: [],
      edit: false,
      editedNote: null,
      searchValue: '',
    };
  },
  created(){
    this.getNotes()
  },
  computed: {
    filterNotes() {
      return this.searchValue ? this.notes.filter(note => note.title.toLowerCase().includes(this.searchValue.toLowerCase())) : this.notes
    }
  },
  methods: {
    delNote(note) {
      this.notes = this.notes.filter(el => el.id != note.id)
    },
    change(note) {
      this.isModalOpen = this.edit = true
      let currentNote =this.notes.find(el => el.id == note.id)
      this.editedNote = currentNote
      
    },
    changeNote(newNote) {
      this.notes.forEach(note => {
        if(note.id == newNote.id) {
          note.title = newNote.title
          note.text = newNote.text
          note.date = newNote.date
        }
      })
      this.editedNote = null
    },
    addNote(note) {
      this.notes.push(note)
    },
    getNotes() {
      let localNotes = localStorage.notes
      if(localNotes) {
        this.notes = JSON.parse(localNotes)
      }
    }
  },
  watch: {
    notes: {
      handler(newValue) {
        localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true
    }
  }
};
</script>
