import { createI18n } from 'vue-i18n'


import ru from '@/i18n/locales/ru.json'
import uz from '@/i18n/locales/uz.json'
export default createI18n({
    locale: 'ru', // задает язык на сайте по умолчанию
    fallbackLocale: 'uz', //задает тот язык, который будет, если нет языка в locale
    legacy: true, // true-когда options.api, false-когда composition.api
    globalInjection: true, //пишем всегда
    messages: {ru, uz} //тут храним все названия для языка
})


